import InputHandler from './InputHandler';
import Vector2 from './Vector2';

const createViewportAwareInputListener = (handler, viewport) => {
  return (x, y, id) => {
    const {
      x: vX,
      y: vY,
      pixelRatio
    } = viewport.getViewportInfo();
    x = x * pixelRatio - vX;
    y = y * pixelRatio - vY;
    handler(x, y, id);
  };
};

export default ((canvas, viewport) => {
  const inputHandler = new InputHandler(canvas);
  const screenCoord = new Vector2();
  const worldCoord = new Vector2();
  return {
    isTouched() {
      return inputHandler.isTouched();
    },

    cleanup() {
      inputHandler.cleanup();
    },

    addEventListener(event, listener) {
      inputHandler.addEventListener(event, createViewportAwareInputListener(listener, viewport));
    },

    getX() {
      const {
        x: vX,
        pixelRatio
      } = viewport.getViewportInfo();
      return inputHandler.getX() * pixelRatio - vX;
    },

    getY() {
      const {
        y: vY,
        pixelRatio
      } = viewport.getViewportInfo();
      return inputHandler.getY() * pixelRatio - vY;
    },

    getTouchedWorldCoord(camera) {
      if (!camera) {
        camera = viewport.getCamera();
      }

      screenCoord.set(this.getX(), this.getY());
      camera.unprojectVector2(worldCoord, screenCoord);
      return worldCoord;
    }

  };
});