import { mat4 } from 'gl-matrix';
import { unproject } from 'gdxjs/lib/mathUtils';
import Vector2 from './Vector2';
export default ((width, height, viewportWidth, viewportHeight) => {
  const combined = mat4.create();
  const transform = mat4.create();
  const invOrtho = mat4.create();
  const zoom = mat4.create();
  const lookAtMatrix = mat4.create();
  const position = new Vector2(0, 0);
  const NEAR = 0;
  const FAR = 1;
  const tmp3 = [0, 0, 0];
  const eye = [0, 0, FAR];
  const center = [0, 0, NEAR];
  const up = [0, 1, 0];

  const update = () => {
    mat4.ortho(combined, 0, width, height, 0, NEAR, FAR);
    const x = position.x;
    const y = position.y;
    eye[0] = center[0] = x;
    eye[1] = center[1] = y;
    mat4.identity(transform);
    mat4.lookAt(lookAtMatrix, eye, center, up);
    mat4.mul(transform, transform, lookAtMatrix);
    mat4.mul(transform, transform, zoom);
    mat4.mul(combined, combined, transform);
    mat4.invert(invOrtho, combined);
  };

  update();
  return {
    combined,

    getPosition() {
      return position;
    },

    zoomTo(x, y, scale) {
      mat4.translate(zoom, zoom, [x, y, 0]);
      mat4.scale(zoom, zoom, [scale, scale, 1]);
      mat4.translate(zoom, zoom, [-x, -y, 0]);
      update();
    },

    setMatrix(mat) {
      mat4.copy(combined, mat);
      mat4.invert(invOrtho, combined);
    },

    setYUp(yUp) {
      if (yUp) {
        up[1] = -1;
      } else {
        up[1] = 1;
      }

      update();
    },

    setPosition(x, y) {
      position.set(x, y);
      update();
    },

    unprojectVector2(out, input) {
      tmp3[0] = input.x;
      tmp3[1] = input.y;
      tmp3[2] = 0;
      unproject(tmp3, tmp3, invOrtho, 0, 0, viewportWidth, viewportHeight);
      out.set(tmp3[0], tmp3[1]);
    },

    resize(newWidth, newHeight, newVWidth, newVHeight) {
      width = newWidth;
      height = newHeight;
      newVWidth && (viewportWidth = newVWidth);
      newVHeight && (viewportHeight = newVHeight);
      update();
    }

  };
});